.technology_skills{
    h2{
        @include resize($fontSizeH2);
    }
    ul{
        @include spaceParagraph;
        list-style: none;
    }
    ul li{
        list-style-type: none;
        display: inline;
        white-space: pre-wrap;
        padding: 0;
        margin: 0;
        @include resize($fontSize);
    }
    li:not(:last-child)::after {
        display: inline-block;
        content: ", ";
    }
}