@media (min-width: 780px){
    .Notfound{
        margin-left:calc(40%); 
        margin-top: calc(10%);
        margin-bottom: calc(10%);
        position: absolute;
    }
}

@media (max-width: 780px){
    .Notfound{
        align-content: center;
        text-align: center;
        margin-left: 20px;
        margin-top: 200px;
    }
}