@use '../utils' as *;

.home{
    @include list-styles;   
    @include fontFamily;
    background-color: white;
    right: 5.0em;
    @include display-li;
    @include positionHome;

    h2{
        @include resize($fontSizeH2);
        @include spaceH2;
    }

    .general_paraf{
        li{
            a{
                text-decoration: none;
            }
            @include display-li;
            @include list-styles;
        }
        p{
            @include resize($fontSize);
            @include spaceParagraph;
        }
    }

    .cabeçalho{
        @media (min-width:980px) {
            img{
                display: none;
            }   
        }
        img{
            margin-left: 30px;
            border-radius: 5%;
            height: auto;
            max-width: 100%;
        }
    }
}

footer{
    @include footer;
}

.eu{   
    @include img;    
}