@use 'variables';

@mixin list-styles{
    list-style-type: none;
    text-decoration-line: none;
}

@mixin spaceParagraph{
    margin: 15px 0px 15px 0px;
}

@mixin spaceH2{
    margin: 25px 0px 25px 0px;
}

@mixin positionHome {
    @media (min-width: 981px){
        left:calc(15.0em);
        position: absolute;
    }
    @media (min-width: 1450px){
        left:calc(17.0em);
        position: absolute;
    }
    @media (min-width: 2500px){
        left:calc(19.0em);
        position: absolute;
    }
    @media (max-width: 980px){
    margin-right:calc(40px);
    top: 0;
    height:70%;
}
}

@mixin img{
    @media (max-width: 980px){
        display: none;
    }
    border-radius: 10%;
}

@mixin footer{
    bottom: 0;
    margin-top:20%;
    text-align: right;
}

@mixin display-li {
    display: inline-block;
    padding:15px;
    list-style-type: none;
    text-decoration: none;
    @media (max-width:980px){
        padding: 3px;
    }
}

@mixin fontFamily {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

@mixin resize($size) {
    $result: $size;
    @media only screen and (max-width: $large-display){
        $result: $size * 2.5;
        font-size: $result;
        font-weight: 450;
    }
    @media only screen and (max-width: $desktop){
        $result: $size * 2.5;
        font-size: $result;
        font-weight: 450;
    }
    @media only screen and (max-width: $laptop){
        $result: $size * 2.5;
        font-size: $result;
        font-weight: 450;
    }
    @media only screen and (max-width: $tablet){
        $result: $size * 2.5;
        font-size: $result;
        font-weight: 300;
    }
    @media only screen and (max-width: $phone) {
        $result: $size * 2.5;
        font-size: $result;
        font-weight: 300;
    }
}

@mixin Education {
    margin: 20px 0px 0px 20px;
}